
// COLOR

$dark-tint-primary: rgba(0,0,0,0.87);
$dark-tint-secondary: rgba(0,0,0,0.54);
$dark-tint-disabled: rgba(0,0,0,0.54);
$dark-tint-divider: rgba(0,0,0,0.12);

$light-tint-primary: rgba(255,255,255,1);
$light-tint-secondary: rgba(255,255,255,0.70);
$light-tint-disabled: rgba(255,255,255,0.50);
$light-tint-divider: rgba(255,255,255,0.12);



$dark-grey: #323232;

$amber: #ffC107;
$amber700: #FFA000;

$blue: #2196F3;
$blue200: #90CAF9;
$blue700: #1976d2;
$blue900: #0D47A1;

$cyan: #00BCD4;
$cyan200: #80DEEA;
$cyan700: #0097A7;
$cyan200: #18FFFF;

$green: #4CAF50;
$green700: #388E3C;

$grey: #9E9E9E;
$grey700: #616161;

$indigo: #3F51B5;
$indigo700: #303F9F;

$lime: #CDDC39;
$lime200: #E6EE9C;
$lime700: #AFB42B;
$limeA400: #C6FF00;

$orange: #FF9800;
$orange700: #F57C00;

$pink: #E91E63;
$pinkA100: #FF80AB;
$pink100: #F8BBD0;
$pink200: #F48FB1;

$purple: #673AB7;
$purple300: #BA68C8;
$purple700: #512DA8;

$red: #F44336;


$theme-blue: #284B63;
$theme-teal: #77A0A0;
$theme-lt: #D9D9D9;
$theme-dk: #909090;
$theme-green: #77A076;
$theme-white: white;


$shades: (
    dark: (
        primary: $dark-tint-primary,
        secondary: $dark-tint-secondary,
        disabled: $dark-tint-disabled,
        divider: $dark-tint-divider
    ),
    light: (
        primary: $light-tint-primary,
        secondary: $light-tint-secondary,
        disabled: $light-tint-disabled,
        divider: $light-tint-divider
    )
);

@function get-shade-for-theme-fill($fill) {
    @if $fill == $theme-blue or $fill == $theme-dk {
        @return map-get($shades, light);
    }
    @return map-get($shades, dark);
}

// $title-fill: white;
// $title-tint: $dark-tint-primary;

$title-fill: $theme-lt;
$title-tint: $dark-tint-primary;
$title-divider: $dark-tint-divider;
$title-secondary: $dark-tint-secondary;
$title-disabled: $dark-tint-disabled;
$title-shade: map-get($shades, dark);

$profile-fill: $theme-blue;
$profile-tint: $light-tint-primary;
$profile-divider: $dark-tint-divider;
$profile-secondary: $dark-tint-secondary;
$profile-disabled: $dark-tint-disabled;
$profile-shade: map-get($shades, light);


$xp-fill: $theme-teal;
$xp-tint: $dark-tint-primary;
$xp-divider: $dark-tint-divider;
$xp-secondary: $dark-tint-secondary;
$xp-disabled: $dark-tint-disabled;
$xp-shade: map-get($shades, dark);


$tech-fill: $theme-lt;
$tech-tint: $light-tint-primary;
$tech-divider: $dark-tint-divider;
$tech-secondary: $dark-tint-secondary;
$tech-disabled: $dark-tint-disabled;
$tech-shade: map-get($shades, dark);


$edu-fill: $theme-green;
$edu-tint: $dark-tint-primary;
$edu-divider: $dark-tint-divider;
$edu-secondary: $dark-tint-secondary;
$edu-disabled: $dark-tint-disabled;
$edu-shade: map-get($shades, dark);


$connect-fill: $theme-lt;
$connect-tint: $dark-tint-primary;
$connect-divider: $dark-tint-divider;
$connect-secondary: $dark-tint-secondary;
$connect-disabled: $dark-tint-disabled;
$connect-shade: map-get($shades, dark);



// SIZE

$header-height: 56px;


$headers: (title, profile, experience, technology, education, connect);
$views: (
    (
        name: title,
        title: Title,
        fill: $title-fill,
        tint: $title-tint,
        shade: $title-shade,
        index: 0
    ),
    (
        name: profile,
        title: Profile,
        fill: $profile-fill,
        tint: $profile-tint,
        shade: $profile-shade,
        index: 1
    ),
    (
        name: experience,
        title: Experience,
        fill: $xp-fill,
        tint: $xp-tint,
        shade: $xp-shade,
        index: 2
    ),
    (
        name: technology,
        title: Technology,
        fill: $tech-fill,
        tint: $tech-tint,
        shade: $tech-shade,
        index: 3
    ),
    (
        name: education,
        title: Education,
        fill: $edu-fill,
        tint: $edu-tint,
        shade: $edu-shade,
        index: 4
    ),
    (
        name: connect,
        title: Connect,
        fill: $connect-fill,
        tint: $connect-tint,
        shade: $connect-shade,
        index: 5
    )
);